// 定义平均分查询功能提交函数
const avgSubmit = () => {
  if (formData.value.condition === null || formData.value.content === null) {
    message.error('请选择查询条件！');
    return
  }
  dataAvg.value = []
  openAvgTable.value = true
  if(formData.value.content === -1){
    getAvgResAllApi(formData.value.condition).then(res => {
      if(res.data.code === 200){
        if (formData.value.condition === 'lxyz') {
          selectedLevel.value = 'LXYZ类型'
          selectedGroup.value = '全部'
        } else {
          selectedLevel.value = '业务类型'
          selectedGroup.value = '全部'
        }

        const result = res.data.data.dataList

        for(let i=0;i<result.length;i++){
          dataAvg.value.push(
            {
              lxyzType: result[i].lxyz,
              businessType: result[i].business,
              totalScore: result[i].totalScore,
              openProgressive: result[i].scoreList[0],
              winBelief: result[i].scoreList[1],
              innovationRealism: result[i].scoreList[2],
              trustHelp: result[i].scoreList[3],
              respectEquality: result[i].scoreList[4],
              diligenceExcellence: result[i].scoreList[5],
              missionAccomplished: result[i].scoreList[6],
              globalView: result[i].scoreList[7],
              selfReflection: result[i].scoreList[8]
            }
          )
        }
