    getAvgResApi(formData.value.condition, formData.value.content).then(res => {
    if (res.data.code === 200) {
      if (res.data.data.lxyz) {
        selectedLevel.value = 'LXYZ类型'
        selectedGroup.value = res.data.data.lxyz
      } else {
        selectedLevel.value = '业务类型'
        selectedGroup.value = res.data.data.business
