          innovationRealism: res.data.data.scoreList[2],
          trustHelp: res.data.data.scoreList[3],
          respectEquality: res.data.data.scoreList[4],
          diligenceExcellence: res.data.data.scoreList[5],
          missionAccomplished: res.data.data.scoreList[6],
          globalView: res.data.data.scoreList[7],
          selfReflection: res.data.data.scoreList[8]
        }
      )
    } else {
      message.error('平均分查询失败！' + res.data.msg)
    }
  }).catch(err => {
