// 定义修改平均分系数数据
const isUpdateAvgOpen = ref(false)
const updateAvgData = ref(null)
const handleUpdateAvg = () => {
  let updateAvgValue = updateAvgData.value;

  // 检查置信度是否为空
  if (updateAvgValue === null || updateAvgValue === '') {
    message.error('请填写平均分系数！');
    return; // 退出函数
  }

  // 检查置信度是否是有效的数字
  if (isNaN(updateAvgValue)) {
    message.error('系数必须是一个有效的数字！');
    return; // 退出函数
  }

  // 检查置信度是否在合法范围 [0, 1] 内
  if (updateAvgValue < 0 || updateAvgValue > 1) {
    message.error('系数必须在 0 到 1 之间！');
    return; // 退出函数
  }

  getUpdateAvgApi(formData.value.condition, setAvgContent.value, updateAvgData.value).then(res => {
    if (res.data.code === 200) {
      message.success('修改成功！')
      isUpdateAvgOpen.value = false
      updateAvgData.value = null

      // 刷新表格数据
      avgSubmit()
    } else {
      message.error('修改失败！' + res.data.msg)
    }
  }).catch(err => {
    console.log('请求失败！', err)
  })
}

