// 设置置信度相关配置
const isConfidenceModalOpen = ref(false)
const confidenceForm = ref({
  confidence: "", // 默认置信度值
});
const setConfidenceUserId = ref('')
const openConfidenceModel = (record) => {
  setConfidenceUserId.value = record.userId
  isConfidenceModalOpen.value = true
}
const handleConfidenceModalCancel = () => {
  isConfidenceModalOpen.value = false
}
const handleConfidenceSubmit = () => {

  let confidenceValue = confidenceForm.value.confidence;

  // 检查置信度是否为空
  if (confidenceValue === null || confidenceValue === "") {
    message.error("请填写置信度！");
    return; // 退出函数
  }

  // 检查置信度是否是有效的数字
  if (isNaN(confidenceValue)) {
    message.error("置信度必须是一个有效的数字！");
    return; // 退出函数
  }

  // 检查置信度是否在合法范围 [0, 2] 内
  if (confidenceValue < 0 || confidenceValue > 2) {
    message.error("置信度必须在 0 到 2 之间！");
    return; // 退出函数
  }

  getAssessorConfidenceApi(setConfidenceUserId.value, confidenceForm.value.confidence)
    .then((res) => {
      if (res.data.code === 200) {
        // 提示设置成功
        message.success("设置成功！");
        // 关闭弹窗
        isConfidenceModalOpen.value = false;
        // 刷新表格
        getScoreTableData();
      } else {
        message.error("设置失败！"+ res.data.msg);
      }
    })
    .catch((err) => {
      message.error("请求失败！"+ err);
    });
};

// 获取当前时间并格式化
const getCurrentDateTime = () => {
  const now = new Date();
  const year = now.getFullYear();
  const month = (now.getMonth() + 1).toString().padStart(2, "0"); // 月份是从0开始的
  const day = now.getDate().toString().padStart(2, "0");
  return `${year}-${month}-${day}`;
};

// 导出得分看板数据
const exportData = () => {
  getExportDataExcelApi()
    .then((res) => {
      // 判断响应是否为文件流
      if (res && res.data) {
        const blob = new Blob([res.data], { type: 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet' });
        const link = document.createElement('a');
        const url = URL.createObjectURL(blob);

        // 使用当前日期来动态生成文件名
        const fileName = `得分看板数据${getCurrentDateTime()}.xlsx`;

        link.href = url;
        link.download = fileName;  // 设置文件名
        link.click();  // 触发下载
        message.success("得分看板数据导出成功！");
      } else {
        message.error("导出失败！未知错误");
      }
    })
    .catch((err) => {
      console.error('请求失败：', err);
      message.error("请求失败！");
    });
}

// 初始加载数据
onMounted(() => {
  fetchDepartmentList()
  getScoreTableData()
  getStepTime()
  updateScrollHeight();
  window.addEventListener('resize', updateScrollHeight);
})

// 在组件卸载时移除事件监听
onUnmounted(() => {
  window.removeEventListener('resize', updateScrollHeight);
});
</script>

