import axios from "axios";

//获取得分看板数据
const getScoreTableDataApi = (pageNum, pageSize, name, department, orderAt) => new Promise((resolve, reject) =>{
    let data = {
        pageNum: pageNum,
        pageSize: pageSize,
        name: name,
        department: department,
        orderAt: orderAt
      };
    axios({
        method:'post',
        url:'/api/getBoard',
        headers:{
            "Content-Type": "application/json",
        },
        data: JSON.stringify(data),
    }).then((response)=>{
        resolve(response)
    }).catch((error)=>{
        reject(error.response)
    })
})

//查看某人得分详情
const getScoreDetailApi = (id) => new Promise((resolve, reject) =>{
    axios({
        method:'get',
        url:'/api/getBoard/detail',
        headers:{
            "Content-Type": "application/json",
        },
        params: {
            id: id
        },
    }).then((response)=>{
        resolve(response)
    }).catch((error)=>{
        reject(error.response)
    })
})

